import React from 'react';
import { Box } from 'rebass';
import Title from '../../general/title';
import './coronavirus.css';
import content from './content.js';

const Coronavirus = () => {

    let faqs = [];

    for (let i = 0; i < content.length; i++) {
        faqs.push((
            <div>
                <p>{faq.question}</p>
                {faq.answer}
            </div>
        ))
    }

    return (
        <div className='coronavirus-page'>
            <Title title="McMurtry COVID-19 FAQs" width={[300, 500]} />
            <Box width={[0.9, 0.55]} ml='auto' mr='auto'>
                <p>
                    Hey McMurtry!,
            </p>
                <p>
                    As you know, Rice students living on campus have been asked to vacate the campus by March 25th, unless they petition and receive special consideration from Dean Gorman. To complement the information you are receiving from the administration, the McTeam created this FAQ that addresses some of your questions regarding the move-out process at McMurtry.
            </p>
                <p>Murt Love!</p>
                <p>The McTeam</p>
            </Box>
            <Box className="covid12-faqs" ml='auto' mr='auto' width={[0.9, 0.55]}>
                {faqs}
            </Box>
        </div >
    )
}

export default Coronavirus;